import React, { useState } from 'react';
import './Circles.css';

const Circles = ({ selected, setSelected }) => {
  const nums = [1, 2, 3, 4];
  return (
    <div className='Circles'>
      {nums.map(num => (
        <div 
          key={num}
          onClick={() => setSelected(num)}
          className={selected === num ? 'selected' : ''}
        >
          {`${num}`}
        </div>
      ))}
    </div>
  );
};

export default Circles;